.card {
    background: #fff;
    border-radius: 10px;
    height: 240px;
    margin: 1rem;
    margin-top: 12px;
    position: relative;
    width: 270px;
    overflow: hidden;
  }
  
  /* Slide */
   .hover img {
      margin-left: 10px;
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
  }
  .hover img:hover  {
      margin-left: -30px;
  
  }
.wrapper {
    padding-top: 10px;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
  
  }
.gameScore {
    text-align: center;
    height: 70px;
    position: relative;
    line-height: 1em;
    padding: 0;
    margin: 0;
    font-family: 'Sniglet', cursive;
    color:darkblue;
    font-size: 20px;
  }
  
  
  h3.score {
    margin-top: 5px;
    padding: 0;
    font-weight: 600;
    font-family: 'Sniglet', cursive;
  }
  h3.status{
    margin: 0;
    padding: 0;
    color: red;
    font-family: 'Sniglet', cursive;
  }

.App-header {
    background-color: #0a2343;
    color: #fff;
    padding: 20px 20px;
    padding-bottom: 10px;
    text-align: center;
    
  }
  
  .App-title {
    font-size: 4em;
    font-family: 'Sniglet', cursive;
  }
  
  .App-intro {
    font-size: 1.5em;
    font-family: 'Sniglet', cursive;
  }
